import React from 'react';
import ReactDOM from 'react-dom';
import { Container, Label, Card, CardImg, Alert } from 'reactstrap';


import Header from './navs';


class PCRHelp extends React.Component {
  constructor(props) {
    super(props);
  }

  toggleDemo = () => {
    const url = 'https://pan.baidu.com/s/1OaIrEtk1XGEFMEBHlGNcdg';
    window.open(url, '_blank');
  }

  render() {
    return (
      <div>
        <Header />
        <Container>
          <Label sm={2} size="lg"></Label>
          <Card>
            <CardImg top width="100%" src='/static/pcr.jpeg' />
            <Alert color="danger">
               txt 
              <br /><a href="#" onClick={this.toggleDemo} className="alert-link"></a>:bivt
              <br />
              
            </Alert>
            <Alert color="primary">
              GAPDH 
              <br />0 1 2 3 4 5 6  22 23 24
              <br />A
              <br />B
              <br />C
              <br />
              start A1 end A12   A1 - A12 B1 - B12C1 - C12
              <br />
               AVERAGEi = Ai + Bi + Ci/ 3 , (i = 1, 2, 3, 4, ..., 10, 11, 12)
            </Alert>
            <Alert color="success">
              
              <br />0 1 2 3 4 5 6  22 23 24
              <br />D
              <br />E
              <br />F
              <br />
              start D1 end D12   D1 - D12 E1 - E12F1 - F12
              <br /> AVERAGEi = Di + Ei + Fi/ 3 , (i = 1, 2, 3, 4, ..., 10, 11, 12)
              <br />CTi =  AVERAGEi -  AVERAGEi
            </Alert>
            <Alert color="warning">
              
              <br />0 1 2 3 4 5 6  22 23 24
              <br />D
              <br />E
              <br />F
              <br />
              start D1 end D6   D1 - D6 E1 - E6F1 - F6
              <br /> AVERAGEi = CTi / 3 , (i = 1, 2, 3, 4, 5, 6)
            </Alert>
            <Alert color="dark">
              CTi = CTi -  AVERAGEi
              <br /> result = 0.5^CTi
            </Alert>
          </Card>
        </Container>
      </div>
    );
  }
}


ReactDOM.render(
  <PCRHelp />,
  document.getElementById('root')
);



